* {
    padding: 0px;
    margin: 0px;
    border: none;
  }
  
  html {
    box-sizing: border-box;
    font-size: 16px;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }
  
  img {
    width: 100%;
    height: 100%;
  }
  
  ul {
    list-style-type: none;
  }
  
  ul li {
    padding: 0;
    margin: 0;
  }
  
  a {
    text-decoration: none;
  }
  