.goods{padding:2rem 3rem;display:grid;grid-template-columns:repeat(3, 1fr);gap:2rem}.product{padding:1rem;border:1px solid rgba(43,63,80,.13);border-radius:.5rem;display:flex;flex-direction:column;justify-content:space-between;align-items:center;text-align:center}.product:hover{box-shadow:0 0 10px #2b3f50;transform:scale(1.01);transition:.5s}.product__picture{height:256px;width:200px}.product__picture-img{object-fit:contain}.rating{margin-top:50px;position:relative}.stars:before{font-family:"Font Awesome 5 Free";content:"";font-size:18px;color:#cec7c7}.color__stars{overflow:hidden;position:absolute;top:0;left:0}.color__stars:before{font-family:"Font Awesome 5 Free";content:"";color:#ffe100;font-size:18px;font-weight:700}.price{font-size:20px;font-weight:700}.btn{background-color:rgba(208,247,176,.726);border:1px solid rgba(179,218,149,.726);border-radius:5px;cursor:pointer;font-size:16px;margin-top:10px;padding:5px 20px}